import Vue from 'vue'
import App from './App'
import 'common/element-ui'
import 'common/axios.config'
import VueRouter from 'vue-router'
import {activity} from '../../common/router.config'
import tools from '../../common/tools'
import filters from '../../common/filters/' //过滤器
import store from '../../common/store/'

Object.keys(filters).forEach(key => Vue.filter(key, filters[key]));

Vue.use(VueRouter);
Vue.use(tools);

const router = new VueRouter({
  // mode:'history',
  routes:activity
});
/* eslint-disable no-new */
new Vue({
  router,
  store,
  el: '#app',
  template: '<App/>',
  components: {App}
});



